import axios from 'axios'
import { message } from 'ant-design-vue'
// axios
const http = axios.create({
  // cookie
  withCredentials: true
})

// 
http.defaults.baseURL = ''

// 
http.interceptors.request.use(
  config => {
    if (config.methods === 'get') {
      config.params = config.data
      delete config.data
    }
    if (config.url.includes('guoxin')) {
      // 
    } else {
      // -headers
      config.headers['x-hw-id'] = 'de712255-9e37-4544-931f-68016062dfa4'
      config.headers['x-hw-appkey'] = 'ccce26b143c32ebe8bdd2a094942fd0f2ab0b93cb0b25b1869fb5e38c11ccdc0'
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// 
http.interceptors.response.use(
  // 
  res => res.status === 200 ? Promise.resolve(res) : Promise.reject(res),

  // 
  error => {
    const { response } = error

    if (response) {
      // errorHandler(response)
      return Promise.reject(response)
    } else {
      // 
      if (!window.navigator.onLine) {

      } else {
        return Promise.reject(error)
      }
    }
  }
)

// 
const errorHandler = (response) => {
  const { data, status } = response
  switch (status) {
    case 401:
      message.error('')
      break
    case 403:
      message.error('')
      break
    case 404:
      message.error('')
      break
    default:
      message.error(data.message)
  }
}

export default http
